import banner from '../../components/banner.vue'
export default{
  components: {
    banner: banner
  },
  data() {
    return {
      swiperList: '',
      categorys: '',
      id_default: '',           // 默认选中第一个分类
      list: ''
    }
  },
  onLoad() {
    this.getBanners()
    this.getCouponList()
  },
  methods: {
    
    // 获取轮播图
    getBanners() {
      let that= this,
        params= {
          url: that.$api.couponBanners
        }
      that.$httpRequest(params).then(res => {
        that.swiperList= res.data.list
      })
    },
    // 选择分类
    selectTab(id) {
      let that= this
      that.id_default= id
      that.getCouponList(id)
    },
    
    // 优惠券列表
    getCouponList(id, type) {
      let that= this,
        params= {
          url: that.$api.couponCenter,
          data: {
            category_id: id? id: '',
            type: type? type: 'all'
          }
        }
      that.$httpRequest(params).then(res => {
        if(!that.categorys) {
          that.categorys= res.data.coupons.categorys
          that.id_default= res.data.coupons.categorys[0].category_id
        }
        that.list= res.data.coupons.list
      })
    },
    
    // 领券
    getCoupon(id, index) {
      let that= this,
        params= {
          url: that.$api.receiveCoupon,
          data: {
            coupon_id: id
          }
        }
      that.$httpRequest(params).then(res => {
        that.list[index].is_receive= true
        uni.showToast({
          title: '兑换成功'
        })
      })
    }
  },
  
  
  
    
}
